import Store from '@/store/index.js'
import http from '@/utils/http.js'
import EventBus from '@/utils/event-bus.js'
import JsNativeApi from '@/utils/js-native-api.js'
import { log } from '@/utils/log.js'
import * as SystemEvent from '@/code/system-event.js'
import * as Event from '@/code/custom-event.js'
import * as RetCode from '@/code/return-code.js'

let cache = {}

function hasChecked (id) {
  if (!cache[id]) {
    cache[id] = { status: 0 }
  }
  if (cache[id] && cache[id].status !== 0) {
    return true
  }
  return false
}

function checkSteps (mac) {
  if (!Store.getters.isLogined) {
    return
  }
  let figure = Store.state.figures.find(figure => figure.braceletMac === mac)
  if (figure && !hasChecked(figure.id)) {
    let isStaff = Store.getters.isStaff
    let token = Store.state.user.token
    let id = figure.id
    cache[id].status = 1
    JsNativeApi.getBraceletHealthAcitvity().then(result => {
      if (result.type === 'true') {
        let nativeSteps = Number(result.data.step || 0)
        http.post(isStaff ? 'staff/sport' : 'parent/child/sport', isStaff ? {
          token
        } : {
          token,
          idStudent: id
        }, {
          ignore: true
        }).then(res => {
          let steps = res.data.steps || 0
          if (nativeSteps > steps) {
            http.post(isStaff ? 'staff/steps/update' : 'parent/child/steps/update', isStaff ? {
              token,
              steps: nativeSteps
            } : {
              token,
              idStudent: id,
              steps: nativeSteps
            }, {
              ignore: true
            }).then(() => {
              log(`${ id } update server steps finished: native > server`)
              cache[id].status = 0
            }, error => {
              log(`${ id } update server steps failed: ${ error.message }`)
              cache[id].status = 0
            })
          } else {
            log(`${ id } update server steps finished: native <= server`)
            cache[id].status = 0
          }
        }, error => {
          log(`${ id } fetch server steps failed: ${ error.message }`)
          cache[id].status = 0
        })
      } else {
        log(`${ id } fetch native steps failed`)
        cache[id].status = 0
      }
    }, () => {
      log(`${ id } fetch native steps failed: unknown`)
      cache[id].status = 0
    })
  }
}

export function listen () {
  EventBus.$on(Event.LOGIN_OUT, mac => {
    cache = {}
  })

  EventBus.$on(Event.BRACELET_CONNECTED, mac => {
    checkSteps(mac)
  })

  EventBus.$on(SystemEvent.BRACELET_CONNECTED, data => {
    if (data && data.macAddress) {
      checkSteps(data.macAddress)
    }
  })
}
